// scripts/utils.js

function toPx(value, contextElement = document.documentElement) {
    // Create a temporary element inside the context
    const el = document.createElement("div");
    el.style.width = value;
    el.style.position = "absolute";
    el.style.visibility = "hidden";

    // Append it to the context element so relative units (em, %) resolve
    contextElement.appendChild(el);

    // Get computed width in pixels
    const pixels = parseFloat(getComputedStyle(el).width);

    // Clean up
    contextElement.removeChild(el);

    return pixels;
}

function copyTextFromTo(textElem, element) {
    let text = "";
    if (textElem instanceof HTMLElement) {
        text = textElem.value;
    }

    element.textContent = text;
}

function isSameDay(date1, date2) {
    const d1 = new Date(date1);
    const d2 = new Date(date2);

    return (
        d1.getFullYear() === d2.getFullYear() &&
        d1.getMonth() === d2.getMonth() &&
        d1.getDate() === d2.getDate()
    );
}

function arraysAreEqual(arr1, arr2) {
    // Detect if two arrays are the same length
    if (arr1.length !== arr2.length) return;

    // Detect if two arrays have the same values
    for (let i = 0; i < arr1.length; i++) {
        if (arr1[i] !== arr2[i]) return;
    }

    return true;
}